      // 拉取案例详情
      loadExampleDetail(){
        this.$http
          .quoteQuoteDetail({
            quoteId: this.quoteId
          })
          .then(res => {   
            this.exampleInfo = res.data.quote
            this.distance = res.data.distance
       
