              this.navList = [
              {
                state: 0,
                text: '完工照',
                loadingType: 'more',
                orderList: []
              },
              {
                state: 1,
                text: '设计图',
                loadingType: 'more',
                orderList: []
              },
              {
                state: 2,
                text: '材料品牌',
                loadingType: 'more',
                orderList: []
              }]
              
              this.isComplete = true
              uni.setNavigationBarTitle({
                title: '案例详情' 
              })
              
            }else{
              this.navList = [{
                state: 0,
                text: '工地实况',
                loadingType: 'more',
                orderList: []
              },
              {
                state: 1,
                text: '设计图',
                loadingType: 'more',
                orderList: []
              },
              {
                state: 2,
                text: '材料品牌',
                loadingType: 'more',
                orderList: []
              }]
              
              uni.setNavigationBarTitle({
                title: '工地实况' 
              })
            }
            
            this.isCollect = res.data.isCollect
            this.isLike = res.data.isLike
            this.memberInfo = res.data.member
            this.designImgList = res.data.quote.styleImgs
            this.quoteContentList = res.data.quote.content
            // console.log(this.quoteContentList)
          })
          .catch(err => {
