      },
      
      // 拉取案例详情swiper
      loadExampleInfo(){
        if(this.currentIndex === 0){
          this.quoteQuoteDynamicList()
        }else if(this.currentIndex === 1){
          // this.quoteDesignImgList()
        }else if(this.currentIndex === 2){
          this.quoteQuoteMaterials()
        }
      },
      
      // 拉取材料清单
      quoteQuoteMaterials(){
        this.$http
          .quoteQuoteMaterials({
            quoteId: this.quoteId
          })
          .then(res => {
            this.MaterialsInfo = res.data
          })
          .catch(err => {
            console.log(err)
          })
