      // 发送自定义案例消息
      handleSendMsg(e){
        const self = this;
        
        // if (!self.pageLoading) {
          // self.pageLoading = !0;
          if(this.loginStatus !== 0){
            // 拉取商家融云userid
            uni.showLoading({
            	title: "连接中"
            });
            self.$http
              .quoteOnline({
                quoteId:self.quoteId
              })
              .then(res => {
                const topRes = res
                if(topRes.data === this.userInfo.userId){
                  uni.hideLoading()
                  // self.pageLoading = !1;
                  uni.showModal({
                      title: '提示',
                      showCancel: false,
                      content: '请更换账号咨询',
                      success: function (res) {
                          if (res.confirm) {
                              console.log('用户点击确定');
                          } else if (res.cancel) {
                              console.log('用户点击取消');
                          }
                      }
                  });
                }else{
                  if(topRes.code === 200){
                    
                    let textType,coverImgs       
                    if(self.isComplete){
                      textType = 1
                      coverImgs=self.exampleInfo.coverImgs[0]
                    }else{
                      textType = 2
                      coverImgs= self.dynamicList.length === 0?'/static/example/default.jpg':self.dynamicList[0].imgList[0].imgUrl
                    }
                    let sendCnt = {
                      title: self.exampleInfo.customerHouseAddress,
                      decorateType: self.exampleInfo.decorateType,
                      customerHouseType: self.exampleInfo.customerHouseType,
                      address: self.exampleInfo.customerGpsAddress,
                      price: self.exampleInfo.finalPrice,
                      quoteId: self.exampleInfo.quoteId,
                      textType: textType,
                      img: coverImgs
                    }
                    
                    if(self.userInfo.isSubscribe === 1){
                      self.imCheckOnline(topRes.data)
                      self.handleSendMsgImpl(topRes,sendCnt) 
                    }else{     
                      wx.requestSubscribeMessage({
                        tmplIds: ['58HkYaZbPdos9agAkBJjpSy8II2elVNTTmG2Mki5o58'],
                        success (res) {
                          console.log(res)
                          // 授权失败
                          if(res['58HkYaZbPdos9agAkBJjpSy8II2elVNTTmG2Mki5o58'] === 'reject'){
                            console.log("授权失败")
                            self.imCheckOnline(topRes.data)                      
                            self.handleSendMsgImpl(topRes,sendCnt)  
                          }
                          // 授权成功
                          if(res['58HkYaZbPdos9agAkBJjpSy8II2elVNTTmG2Mki5o58'] === 'accept'){
                            console.log("授权成功")
                            self.chatSubscribe()
                            self.imCheckOnline(topRes.data)         
                            self.handleSendMsgImpl(topRes,sendCnt)       
                          }
                        }
                      })  
                    }            
                  }else{ 
                    uni.hideLoading()
                    // self.pageLoading = !1;
                    uni.showModal({
                        title: '提示',
                        showCancel: false,
                        confirmText:'关闭',
                        content: res.msg,
                        success: function (res) {
                            if (res.confirm) {
                                console.log('用户点击确定');
                            } else if (res.cancel) {
                                console.log('用户点击取消');
                            }
                        }
                    });
                  }
                } 
              })
              .catch(err => {
                console.log(err)
              })
          }else{
            this.show = true
            // self.pageLoading = !1;
          }
        // } 
      },

      // 发送消息-实现
      handleSendMsgImpl(topRes,sendCnt){
        const self = this
        
        // 查询对方用户信息
        self.$http.imUserGet({
          id: topRes.data
        }).then(result => {
                   
          uni.hideLoading()
          app.globalData.Services.Message.sendRegister(function(resInfo){    
            self.$openPage({ name: 'chat',query:{ targetId: topRes.data + '',avatarUrl: result.data.userPortrait, userName: result.data.userName}})
          },{
            targetId: topRes.data+ '',
            content:sendCnt,
          },{
            id:result.data.userId,
            portrait :result.data.userPortrait,
            name :result.data.userName,
            meid:self.userInfo.userId,
            meportrait :self.userInfo.avatarUrl,
            mename :self.userInfo.nickName,
            companyName: self.memberInfo.companyName
          })
                            
        }) 
      },


      imCheckOnline(id){
        const self = this
        self.$http
          .imCheckOnline({
            id
          })
          .then(res => {
            
            if(res.status !== '1'){
              console.log("对方不在线")
              self.$http
                  .sendSubscribeMsg({
                    toUserId:id,
                    message: '你有一条新消息！'
                  })
                  .then(res => {
                    console.log("小程序订阅消息发送成功")
                  })
                  .catch(err => {
                    console.log(err)
                  }) 
            }
            
          })
          .catch(err => {
            console.log(err)
          }) 
      },
      
      
      // 后端埋点 告知后端推送订阅消息
      chatSubscribe(){
        const self = this
        self.$http
          .weChatSubscribe({})
          .then(res => {
              self.userInfo.isSubscribe = 1
              self.$store.commit('setUserInfo',  this.userInfo);
          })
          .catch(err => {
            console.log(err)
          }) 
      },
      
      
      // 拨打电话
      handleCallPhone(phone) {
        uni.showModal({
            title: '提示',
            content: '是否拨通联系电话'+ phone,
            success: function (res) {
                if (res.confirm) {
                  uni.makePhoneCall({
                    phoneNumber: phone // 仅为示例
                  })
                } else if (res.cancel) {
                    console.log('用户点击取消');
                }
            }
        });
      },
      
      // 收藏、取消收藏
      handleCollect(e){
        const self = this
        
        if(this.loginStatus !== 0){
          this.$http
            .quoteLike({
              quoteId:this.quoteId,
              type: 2,
            })
            .then(res => {
              self.isCollect = !self.isCollect 
            })
            .catch(err => {
              console.log(err)
            })
        }else{
          this.getUserInfo(e,'collect')
        }
          
      },
      
      // 点赞、取消点赞
      handleSupport(e){
      
        const self = this    
        
        if(this.loginStatus !== 0){
          this.$http
            .quoteLike({
              quoteId:this.quoteId,
              type: 1,
            })
            .then(res => {
              
              if(self.isLike){
                self.exampleInfo.likeNum -= 1
              }else{
                self.exampleInfo.likeNum += 1
              }
              self.isLike = !self.isLike 
            })
            .catch(err => {
              console.log(err)
            })
        }else{
          this.getUserInfo(e,'support')
        }
      },
      
      
      /**
       * 微信用户授权
       * @param {Object} e
       */
      getUserInfo(e,type) {
    
        console.log('用户拉取授权')
      	if (e.detail && e.detail.rawData) {
      		this.wxUserLogin(e.detail, type);
      	} else {
      		uni.showModal({
      			title: '提示',
      			content: Messages.APPLY_AUTH,
      			showCancel: false,
      			confirmColor: '#fa5151'
      		})
      	}
      },
      
      
      /**
       * 微信用户授权登录
       * @param {Object} detail
       */
      wxUserLogin(detail, type) {
        console.log(detail)
        
      	let that = this;
      	uni.showLoading({
      		title: Messages.AUTH_LOADING,
      	})
      	uni.login({
      		provider: 'weixin',
      		success: function(loginRes) {
      			uni.hideLoading();
      			if (loginRes && loginRes.code) {
      				let params = {
      					code: loginRes.code,
      					rawData: detail.rawData,
      					signature: detail.signature,
      					encryptedData: detail.encryptedData,
      					iv: detail.iv
      				};
      				that.$http.login(params).then(res => {
                 console.log(res)
                 console.log(res.code)
      					if (res && res.code == 200) {
      						let userInfo = {
      							avatarUrl: res.data.avatarUrl,
      							nickName: res.data.nickName,
      							isSubscribe: res.data.isSubscribe,
      							userId: res.data.userId
      						};
      						that.avatarUrl = userInfo.avatarUrl;
      						that.nickName = userInfo.nickName;
      						that.$store.commit('setToken', res.data.token);
      						that.$store.commit('setUserInfo', userInfo);
      						that.$store.commit('setLoginStatus', 1);
      						that.initSDK(that);
                        
                  if(type === 'support'){
                    that.handleSupport()
                  } else if(type === 'collect'){
                    that.handleCollect()
                  } 
      					}
      				})
      			} else {
      				uni.showModal({
      					title: "提示",
      					content: Message.FAIL_AUTH,
      					showCancel: false
      				})
      			}
      		}
      	});
      },
      
      initSDK(context) {
      
      	const self = this
      
      	let appKey = context.data.appKey;
      	let token = context.data.token;
      
      	if (appKey == '' || token == '') {
      		// return showPrompt('必须提供 appKey 和 token');
      	}
      
      	this.$http
      		.imUserRegister({
      			id: self.userInfo.userId,
      			name: self.userInfo.nickName,
      			portraitUri: self.userInfo.avatarUrl,
      		})
      		.then(res => {
      			console.log(res)
      
      			// 公有云初始化
      			let modules = {
      				//protobuf: 'https://cdn.ronghub.com/protobuf-2.3.1.min.js' //支持http(s)网络路径、本地相对路径,
      				RongIMLib: RongIMLib,
      				protobuf: null
      			};
      
      			let params = {
      				appKey: appKey,
      				token: token,
      				connectToken: res.data.token
      			};
      
      			let callbacks = {
      				receiveNewMessage: function(message) {
      					uni.$emit('newMessage', message)
      				},
      				getCurrentUser: function(userInfo) {
      				},
      				onTokenIncorrect: function() {
      				},
      				watchConversationList: function(list) {     
      				}
      			}
      			app.globalData.Services.init(params, callbacks, modules);
      		})
      		.catch(err => {
      			console.log(err)
      		})
      },
      
      
      
